body {
  margin: 0;
  padding: 0;
  font-family: "AvenirNext",Avenir,"Helvetica Neue",Helvetica,Arial,sans-serif;
  color: rgba(48, 48, 48, 0.96);
  text-transform: uppercase;
  background: -webkit-gradient(linear, left top, left bottom, from(#94ffec), to(#14adff));
  background: -webkit-linear-gradient(#94ffec, #14adff);
  background: -o-linear-gradient(#94ffec, #14adff);
  background: linear-gradient(#94ffec, #14adff);
  height: 100vh;
}



